.box {
  background-color: #ffcdb2;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0;
  color: #6d6875;
  border-radius: 10px;
  animation: 2s ease-out 0s 1 slideInLeft;
  margin-bottom: 10px;
}

@keyframes slideInLeft {
  0% {
    transform: translateX(-100%);
  }
  100% {
    transform: translateX(0);
  }
}

.header {
  background-color: #ffe4d6;
  width: 100%;
  border-radius: 10px 10px 0 0;
  text-align: center;
}

.name {
  font-size: 35px;
  font-weight: 700;
  text-transform: uppercase;
}

.projects {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin: 0;
}

@media screen and (min-width: 768px) {
  .projects {
    flex-direction: row;
  }
}

.items {
  display: flex;
  flex-direction: column;
  margin: 0;
  width: 100%;
  padding-bottom: 20px;
}

.items:not(:last-child) {
  border-bottom: solid 4px #6d6875;
}

@media screen and (min-width: 768px) {
  .items {
    padding-bottom: 30px;
  }
}

@media screen and (min-width: 768px) {
  .items:not(:last-child) {
    border-bottom: solid 0px #ffcdb2;
    border-right: solid 4px #6d6875;
  }
}

.items:hover {
  box-shadow: 0px 3px 1px $lightGray, 0px 1px 2px $portfolioShadow,
    0px 2px 2px $blocksShadowColor;
  cursor: pointer;
  transition: box-shadow 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.items:hover .animation {
  transform: translateY(0);
  transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.animation {
  position: absolute;
  display: flex;
  content: '';
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background-color: #6d6875b9;
  color: #ffe4d6;
  transform: translateY(100%);
  transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.describe {
  position: absolute;
  top: 0;
  left: 0;
  font-size: 18px;
  font-weight: 500;
  padding: 40px;
}

.block {
  position: relative;
  overflow: hidden;
  display: flex;
  justify-content: center;
}

.image {
  width: 200px;
  height: 150px;
  padding-top: 20px;
}

@media screen and (min-width: 768px) {
  .image {
    padding-top: 30px;
  }
}

.block .image {
  display: block;
}

.title-box {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  align-content: center;
}

.title {
  text-align: center;
  font-size: 30px;
  font-weight: 600;
  margin: 0;
}

.languages {
  text-align: center;
  
}

.link {
  display: flex;
  justify-content: center;
}

.icon-site {
  fill: #ffcdb2;
  padding: 3px;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  border: 1px solid #6d6875;
  background-color: #6d6875;
  margin-right: 20px;
}

.icon-site:hover {
  fill: #6d6875;
  border: 1px solid #ffe4d6;
  background-color: #ffe4d6;
  border: 1px solid #6d6875;
  transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.icon-git {
  fill: #6d6875;
  width: 36px;
  height: 36px;
}

.icon-git:hover {
  fill: #ffe4d6;
  border: 1px solid #6d6875;
  background-color: #6d6875;
  border-radius: 50%;
  transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
}
